<template>
  <div class="select-none">
    <p class="text-xs text-left text-gray-460"></p>
    <div ref="sliderBg" :class="`${prefixCls}`">
      <span :class="`${prefixCls}__min`">1x</span>
      <div ref="slider" :class="`${prefixCls}__progress`" :style="{ width: `${width}px` }">
        <span ref="trunk" :class="`${prefixCls}__point`"><i></i></span>
        <span v-if="ratio > min" :class="`${prefixCls}__current`">{{ ratio }}x</span>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
  import { defineComponent, onMounted, ref, computed, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  export default defineComponent({
    name: 'sliderBar',
    emits: ['update:ratio'],
    setup(_, { emit }) {
      const min = 1;
      const max = 20;
      const slider = ref();
      const sliderBg = ref();
      const trunk = ref();
      const ratio = ref(5);
      const { prefixCls } = useDesign('sliderbar');
      const scale = computed<number>(() => {
        return (ratio.value - min) / (max - min);
      });
      const width = computed<number>(() => {
        if (slider.value) {
          return sliderBg.value.offsetWidth * scale.value;
        }
        return 0;
      });
      onMounted(() => {
        trunk.value.onmousedown = (e: any) => {
          const _width: number = width.value;
          const disX = e.clientX;
          const sliderBgWidth = sliderBg.value.offsetWidth;
          document.onmousemove = (e: any) => {
            const newWidth = e.clientX - disX + _width;
            const scale = newWidth / sliderBgWidth;
            ratio.value = Math.ceil((max - min) * scale + min);
            ratio.value = Math.max(ratio.value, min);
            ratio.value = Math.min(ratio.value, max);
          };

          document.onmouseup = (e: any) => {
            document.onmousemove = document.onmouseup = null;
          };
        };
      });
      watch(ratio, (nVal, oVal) => {
        emit('update:ratio', nVal);
      });
      return { prefixCls, slider, trunk, width, ratio, min, sliderBg };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-sliderbar';
  .@{prefix-cls} {
    background: rgba(#f7f7f7, 0.1);
    @apply relative h-1 w-full rounded-sm mt-3;
    &__min {
      @apply absolute left-0 top-4 text-xs text-gray-460;
    }
    &__progress {
      @apply relative h-1 rounded-sm w-0;
      .bg-gradient();
    }
    &__current {
      right: -6px;
      top: 16px;
      @apply absolute text-xs text-white;
    }
    &__point {
      width: 12px;
      height: 12px;
      right: -6px;
      top: -4px;
      @apply absolute rounded-full flex justify-center items-center cursor-pointer;
      .bg-gradient();
      i {
        width: 6px;
        height: 6px;
        @apply block rounded-full bg-black;
      }
    }
  }
</style>
