<template>
  <div :class="`${prefixCls}__detail`">
    <div class="flex flex-row items-center">
      <div :class="`${prefixCls}__detail--name`" @click.stop="handleShowTokenList">
        <span class="uppercase select-none">
          {{ selectedTokenInfo && `${selectedTokenInfo?.coin}/${selectedTokenInfo?.market}` }}
        </span>
        <!-- <img
          :class="{ trangleUp: showTokenList }"
          :src="require('@/assets/img/trangle_down.png')"
          alt=""
        /> -->
        <svg-icon iconClass="caretDown" class="caretDown" :class="{ trangleUp: showTokenList }" />
        <div class="symbol-list anim-scale-in" v-show="showTokenList">
          <span
            class="uppercase"
            v-for="({ coin, market }, index) in tokens"
            :key="index"
            @click="handleSelectToken(index)"
          >
            {{ coin }}/{{ market }}
          </span>
        </div>
      </div>
      <div :class="`${prefixCls}__detail--price`">
        <h3 :class="colorStyle">{{ price }}</h3>
      </div>
      <div :class="[`${prefixCls}__detail--percent`, colorStyle]">
        <p></p>
        <h4 class="text-green-410">{{ chgSign }} {{ chg }}%</h4>
      </div>
      <div :class="`${prefixCls}__detail--num`">
        <div>
          <ToolTips placement="top" trigger="hover">
            <h5></h5>
            <template v-slot:content>
              <div class="text-left">
                <br />
              </div>
            </template>
          </ToolTips>
          <p>{{ markPrice }}</p>
        </div>
        <div>
          <ToolTips placement="top" trigger="hover">
            <h5></h5>
            <template v-slot:content>
              <div class="text-left">
                <br />
              </div>
            </template>
          </ToolTips>
          <p>{{ indexPrice }}</p>
        </div>
        <div>
          <ToolTips placement="top" trigger="hover">
            <h5>24H</h5>
            <template v-slot:content><div>24H</div></template>
          </ToolTips>
          <p>{{ oneDayVolume }}</p>
        </div>
        <div>
          <ToolTips placement="top" trigger="hover">
            <h5></h5>
            <template v-slot:content>
              <div class="text-left">
                <br />
              </div>
            </template>
          </ToolTips>
          <p>{{ capitalRate }}%</p>
        </div>
      </div>
    </div>

    <!-- <a :class="`${prefixCls}__detail--help`">
      
    </a> -->
  </div>
</template>
<script lang="ts">
  import { computed, defineComponent, onMounted, ref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import EventListener from '@/helper/eventListener';
  import { tradeConfStore, SelectedTokenInfo } from '@/store/modules/tradeConf';
  import { ws } from '@/utils/http/ws/socket';
  import { bigNumberForamte } from '@/utils';
  interface SymbolInfo {
    capitalRate: number;
    chg: number;
    indexPrice: number;
    markPrice: number;
    oneDayVolume: number;
    price: number;
    symbol: string;
  }
  export default defineComponent({
    name: 'symbolDetail',
    setup() {
      const { prefixCls } = useDesign('symbol');
      const selectedTokenIndex = ref(0);
      const showTokenList = ref(false);
      const symbolInfo = ref<SymbolInfo | null>(null);
      const tokens = computed(() => {
        return tradeConfStore.getTokens;
      });
      const selectedTokenInfo = computed(() => {
        if (tokens.value.length) {
          return tokens.value[selectedTokenIndex.value];
        }
        return null;
      });

      const price = computed(() => {
        return bigNumberForamte(symbolInfo.value?.price, 2);
      });
      const indexPrice = computed(() => {
        return bigNumberForamte(symbolInfo.value?.indexPrice, 2);
      });
      const markPrice = computed(() => {
        return bigNumberForamte(symbolInfo.value?.markPrice, 2);
      });
      const oneDayVolume = computed(() => {
        return bigNumberForamte(symbolInfo.value?.oneDayVolume);
      });
      const capitalRate = computed(() => {
        return (parseFloat(bigNumberForamte(symbolInfo.value?.capitalRate)) / 100).toFixed(2);
      });
      const chg = computed(() => {
        return (parseFloat(bigNumberForamte(symbolInfo.value?.chg)) / 100).toFixed(2);
      });

      const chgSign = computed(() => {
        return parseFloat(chg.value) >= 0 ? '+' : '';
      });
      const colorStyle = computed(() => {
        return parseFloat(chg.value) >= 0 ? 'text-green-410' : 'text-red-410';
      });
      watch(
        () => selectedTokenInfo.value,
        (val) => {
          if (val) {
            const { coin, market } = val;
            if (coin && market) {
              tradeConfStore.commitSelectedTokenInfo(val as SelectedTokenInfo);
              ws.subscribe(
                `${coin}16${market}`,
                { cmd: 16, c: coin, m: market },
                (data: SymbolInfo) => {
                  console.log('ws-data:', data);
                  symbolInfo.value = data;
                }
              );
            }
          }
        }
      );

      function handleSelectToken(index: number) {
        selectedTokenIndex.value = index;
      }
      function handleShowTokenList() {
        showTokenList.value = !showTokenList.value;
      }
      onMounted(() => {
        EventListener.listen(document.body, 'click', function () {
          showTokenList.value = false;
        });
      });
      return {
        prefixCls,
        tokens,
        selectedTokenInfo,
        handleSelectToken,
        showTokenList,
        handleShowTokenList,
        price,
        indexPrice,
        markPrice,
        chg,
        oneDayVolume,
        capitalRate,
        chgSign,
        colorStyle,
      };
    },
  });
</script>
