import { defHttp } from '@/utils/http/axios';

enum Api {
  FUNFING_PAYMENT = '/user/funding',
}

export function getFundingPaymentApi() {
  return defHttp.request({
    url: Api.FUNFING_PAYMENT,
    method: 'POST',
  });
}
