<template>
  <div :class="`${prefixCls}__list`">
    <div :class="`${prefixCls}__list--item`" class="text-gray-460 mt-3">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
    <div class="pt-5" v-if="pendding"><UiLoading class="big" /></div>
    <UiNoRecord v-if="!lists.length && !pendding" />
    <div
      v-else
      v-for="({ time, name, amount, rate }, index) in lists"
      :key="index"
      :class="`${prefixCls}__list--item`"
      class="text-white"
    >
      <span>{{ dataFormate(time) }}</span>
      <span class="uppercase">{{ name }}</span>
      <span>{{ bigNumberForamte(amount, 2) }}</span>
      <span>{{ bigNumberForamte(rate, 2) }}</span>
    </div>
  </div>
</template>
<script lang="ts">
  import { defineComponent, watch, ref, computed, onMounted } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { getFundingPaymentApi } from '@/api/trade/fundingPayment';
  import { dataFormate } from '@/helper/utils';
  import { scale, bigNumberForamte } from '@/utils';
  import { accountStore } from '@/store/modules/account';
  import BigNumber from 'bignumber.js';
  export default defineComponent({
    name: 'fee',
    setup() {
      const { prefixCls } = useDesign('record');
      const lists = ref([]);
      const pendding = ref(false);

      const account = computed(() => {
        const { connector, address } = accountStore.getAccountState;
        if (!connector || !connector.id || !address) {
          return '';
        }
        return address;
      });
      async function fetchFundingPayment() {
        try {
          pendding.value = true;
          const result = await getFundingPaymentApi();
          lists.value = result;
          pendding.value = false;
        } catch (error) {
          console.error(error);
        } finally {
          pendding.value = false;
        }
      }
      watch(account, (nVal, oVal) => {
        if (nVal) {
          if (nVal !== oVal) {
            fetchFundingPayment();
          }
        } else {
          lists.value = [];
        }
      });
      onMounted(() => {
        account.value && fetchFundingPayment();
      });
      return { prefixCls, lists, dataFormate, bigNumberForamte, pendding };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-record';
  .@{prefix-cls} {
    &__list {
      &--item {
        margin-bottom: 13px;
        &:last-child {
          @apply mb-0;
        }
        @apply flex flex-row text-left text-xs;
        span {
          line-height: 14px;
          @apply flex-1;
          &:nth-child(1) {
            flex-grow: 4;
          }

          &:nth-child(2),
          &:nth-child(3) {
            flex-grow: 3;
          }
          &:last-child {
            flex-grow: 1;
            @apply text-right;
          }
        }
      }
    }
  }
</style>
