<template>
  <div :class="`${prefixCls}`">
    <p :class="`${prefixCls}__title`"></p>
    <div :class="`${prefixCls}__list`">
      <div :class="`${prefixCls}__list--item`" class="text-gray-460 mt-2">
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div
        :class="`${prefixCls}__list--item`"
        v-for="({ time, volume, price }, index) in list"
        :key="index"
      >
        <span class="text-red-410">{{ bigNumberForamte(price, 2) }}</span>
        <span>{{ bigNumberForamte(volume) }}</span>
        <span>{{ dataFormate(time, 'hms') }}</span>
      </div>
    </div>
  </div>
</template>
<script lang="ts">
  import { defineComponent, ref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { tradeConfStore } from '@/store/modules/tradeConf';
  import { ws } from '@/utils/http/ws/socket';
  import { dataFormate } from '@/helper/utils';
  import { bigNumberForamte } from '@/utils';
  export default defineComponent({
    name: 'tradingRecord',
    setup() {
      const { prefixCls } = useDesign('trading-record');
      const list = ref([]);
      watch(
        () => tradeConfStore.getSelectedTokenInfo,
        (val) => {
          if (val) {
            const { coin, market } = val;
            ws.subscribe(`${coin}2${market}`, { cmd: 2, c: coin, m: market }, (data) => {
              console.log('---:', data);
              list.value = data;
            });
          }
        }
      );
      return { prefixCls, list, dataFormate, bigNumberForamte };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-trading-record';
  .@{prefix-cls} {
    width: 260px;
    min-height: 512px;
    @apply bg-gray-490 ml-1 px-4;
    &__title {
      height: 40px;
      line-height: 40px;
      border-bottom: 1px solid rgba(#f7f7f7, 0.1);
      @apply text-white text-left;
    }
    &__list {
      @apply text-xs pb-1 font-roboto;
      &--item {
        margin-bottom: 7px;

        @apply flex flex-row text-gray-460;
        &:last-child {
          @apply mb-0;
        }
        &:first-child {
          margin-bottom: 10px;
          span {
            &:nth-child(2) {
              @apply text-gray-460;
            }
          }
        }
        span {
          line-height: 14px;
          @apply flex-1;
          &:first-child {
            @apply text-left;
          }
          &:nth-child(2) {
            @apply text-white;
          }
          &:last-child {
            @apply text-right;
          }
        }
      }
    }
  }
</style>
