<template>
  <div>
    <p class="text-xs text-gray-460 text-left"></p>
    <div :class="`${prefixCls}`">
      <span
        v-for="(point, index) in slippageLists"
        :key="index"
        :class="{ active: slippage == point }"
        @click="handleClick(point)"
        >{{ point }}%</span
      >
      <input ref="slipPointIpt" type="text" placeholder="" v-model="slippage" />
    </div>
  </div>
</template>
<script lang="ts">
  import { defineComponent, ref, unref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  export default defineComponent({
    name: 'slippage',
    emits: ['update:slippage'],
    data() {
      return {
        slippageLists: [0.01, 0.5, 1],
      };
    },
    setup(_, { emit }) {
      const slippage = ref<number>(0.5);
      const slipPointIpt = ref();
      const { prefixCls } = useDesign('slip-point');
      const handleClick = (val: number) => {
        slippage.value = val;
      };
      watch(slippage, (nVal, oVal) => {
        emit('update:slippage', nVal);
      });
      return { prefixCls, slippage, handleClick };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-slip-point';
  .@{prefix-cls} {
    border-bottom: 1px solid rgba(#f7f7f7, 0.1);
    @apply flex flex-row justify-between mt-3 pb-5;
    span,
    input {
      width: 22%;
      height: 26px;
      line-height: 26px;
      border-color: rgba(#f7f7f7, 0.1);
      @apply border rounded-base text-center text-gray-460 text-xs hover:text-white cursor-pointer;
      &:hover {
        .border-gradient();
      }
      &.active {
        @apply text-white;
        .border-gradient();
      }
    }
    span {
      @apply flex items-center justify-center;
    }
    input {
      @apply placeholder-gray-460 focus:text-white bg-transparent outline-none;
      &:focus {
        .border-gradient();
      }
    }
  }
</style>
