<template>
  <div :class="`${prefixCls}__list`">
    <div :class="`${prefixCls}__list--item`" class="text-gray-460 mt-3">
      <span class="col"></span>
      <span class="col"></span>
      <span class="col"></span>
      <span class="col"></span>
      <span class="col"></span>
      <div class="col">
        <ToolTips class="inline-block" placement="top" trigger="hover">
          <span class="borderd"></span>
          <template v-slot:content
            ><div class="text-left"
              >=+<br /></div
            ></template
          >
        </ToolTips>
      </div>
      <div class="col">
        <ToolTips class="inline-block" placement="top" trigger="hover">
          <span class="borderd"></span>
          <template v-slot:content><div></div></template>
        </ToolTips>
      </div>
      <div class="col">
        <ToolTips class="inline-block" placement="top" trigger="hover">
          <span class="borderd"></span>
          <template v-slot:content
            ><div class="text-left"
              >=+/<br /><br /></div
            ></template
          >
        </ToolTips>
      </div>
      <div class="col">
        <ToolTips class="inline-block" placement="top" trigger="hover">
          <span class="borderd"></span>
          <template v-slot:content
            ><div class="text-left"
              ><br /></div
            ></template
          >
        </ToolTips>
      </div>
      <span class="text-right col"></span>
    </div>
    <div class="pt-5" v-if="pendding"><UiLoading class="big" /></div>
    <UiNoRecord v-if="!lists.length && !pendding" />
    <div
      v-for="(item, index) in lists"
      :key="index"
      :class="`${prefixCls}__list--item`"
      class="text-white"
    >
      <span class="col uppercase">{{ item.name }}</span>
      <span class="text-green-410 col">{{ item.type === 0 ? '' : '' }}</span>
      <span class="col">{{ item.lerve }}X</span>
      <span class="col">{{ bigNumberForamte(item.volume) }}</span>
      <span class="col">{{ bigNumberForamte(item.openPrice, 2) }}</span>
      <span class="col">{{ bigNumberForamte(item.closePrice, 2) }}</span>
      <span class="col">{{ bigNumberForamte(item.margin, 2) }}</span>
      <span class="col">{{ bigNumberForamte(item.marginRate) }}%</span>
      <span class="col">{{ bigNumberForamte(item.unrealized, 2) }}</span>
      <span class="text-right col"
        ><a
          href="javascript:;"
          @click="handleClickModal(ModalType.EARNEST_MONEY, true, item)"
          class="mr-8"
          ></a
        >
        <a href="javascript:;" @click="handleClickModal(ModalType.CLOSE_POSITION, true, item)"
          ></a
        ></span
      >
    </div>
    <Teleport to="#modals">
      <ModalEarnestMoney
        :positionInfo="positionInfo"
        :address="'0x00e7B36aabe3FCdCe306DCD819F022A853cb5578'"
        :open="open.earnestMoney"
        @close="handleClickModal(ModalType.EARNEST_MONEY, false)"
      />
    </Teleport>
    <Teleport to="#modals">
      <ModalClosePosition
        :open="open.closePosition"
        :positionInfo="positionInfo"
        :address="'0x00e7B36aabe3FCdCe306DCD819F022A853cb5578'"
        @close="handleClickModal(ModalType.CLOSE_POSITION, false)"
      />
    </Teleport>
  </div>
</template>
<script lang="ts">
  import { computed, defineComponent, onMounted, reactive, ref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { positionApi } from '@/api/trade/position';
  import { bigNumberForamte } from '@/utils';
  import { accountStore } from '@/store/modules/account';
  enum ModalType {
    EARNEST_MONEY,
    CLOSE_POSITION,
  }
  export default defineComponent({
    name: 'userRecord',
    setup() {
      const { prefixCls } = useDesign('record');
      const open = reactive({
        earnestMoney: false,
        closePosition: false,
      });
      const margin = ref('');
      const marginRate = ref('');
      const openPrice = ref('');
      const closePrice = ref('');
      const volume = ref('');
      const positionInfo = ref();
      const pendding = ref(false);
      const lists = ref<any>([]);
      const account = computed(() => {
        const { connector, address } = accountStore.getAccountState;
        if (!connector || !connector.id || !address) {
          return '';
        }
        return address;
      });
      function handleClickModal(type: ModalType, value: boolean, data?: any) {
        if (value) {
          positionInfo.value = data;
        }
        if (type === ModalType.EARNEST_MONEY) open.earnestMoney = value;
        if (type === ModalType.CLOSE_POSITION) open.closePosition = value;
      }
      async function fetchPosition() {
        try {
          pendding.value = true;
          const result = await positionApi();

          lists.value = result ? result : ([] as any);
          pendding.value = false;
        } catch (error) {
          console.error(error);
        }
      }
      watch(account, async (nVal, oVal) => {
        if (nVal) {
          if (nVal !== oVal) fetchPosition();
        } else {
          lists.value = [];
        }
      });
      onMounted(() => {
        account.value && fetchPosition();
      });
      return {
        prefixCls,
        ModalType,
        handleClickModal,
        open,
        pendding,
        lists,
        bigNumberForamte,
        positionInfo,
      };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-record';
  .@{prefix-cls} {
    &__list {
      &--item {
        margin-bottom: 13px;
        &:last-child {
          @apply mb-0;
        }
        @apply flex flex-row text-left text-xs;
        .col {
          line-height: 14px;
          @apply flex-1;
          &:last-child {
            flex-grow: 1.6;
            @aplpy text-right;
          }
          .borderd {
            border-bottom: 1px dashed rgba(247, 247, 247, 0.1);
            padding-bottom: 4px;
          }
        }
      }
    }
  }
</style>
