<template>
  <div :class="`${prefixCls}`">
    <SymbolDetail />
    <Chart ref="klineRef" v-if="selectedTokenInfo" :symbolInfo="selectedTokenInfo" />
  </div>
</template>
<script lang="ts">
  import { computed, defineComponent, ref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import SymbolDetail from './SymbolDetail.vue';
  import { tradeConfStore } from '@/store/modules/tradeConf';
  import Chart from './Chart.vue';
  export default defineComponent({
    name: 'symbolInfo',
    components: { SymbolDetail, Chart },
    setup() {
      const { prefixCls } = useDesign('symbol-info');
      const klineRef = ref();
      const selectedTokenInfo = computed(() => {
        return tradeConfStore.getSelectedTokenInfo;
      });
      watch(
        () => selectedTokenInfo.value,
        (val, oVal) => {
          if (val?.coin === oVal?.coin) return;
          klineRef.value?.setSymbol(`${val?.coin}${val?.market}`);
        }
      );
      return { prefixCls, selectedTokenInfo, klineRef };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-symbol-info';
  .@{prefix-cls} {
    // height: 300px;
    @apply flex-1;
  }
</style>
