import { VuexModule, Module, getModule, Mutation, Action } from 'vuex-module-decorators';
import store from '@/store';
import { getConfigApi } from '@/api/trade/config';
interface Tokens {
  [key: string]: any;
}
export interface SelectedTokenInfo {
  coin: string;
  market: string;
  decimals: number;
  address: string;
}
interface TradeConfProps {
  tokens: Array<Tokens>;
  selectedToken: SelectedTokenInfo | null;
}

const NAME = 'tradeConf';
@Module({ dynamic: true, namespaced: true, store, name: NAME })
class TradeConf extends VuexModule {
  private config: TradeConfProps = {
    tokens: [],
    selectedToken: null,
  };

  get getTokens() {
    return this.config.tokens;
  }
  get getSelectedTokenInfo(): SelectedTokenInfo | null {
    return this.config.selectedToken;
  }
  @Mutation
  setTokens(tokens): void {
    this.config.tokens = tokens;
  }
  @Mutation
  setSelectedTokenInfo(info: SelectedTokenInfo): void {
    this.config.selectedToken = info;
  }
  @Action
  async fetchTokens() {
    // const result = await getConfigApi();
    const result = [
      {
        coin: 'btc',
        market: 'usdc',
        decimals: 0,
        address: '0x00e7B36aabe3FCdCe306DCD819F022A853cb5578',
      },
      {
        coin: 'eth',
        market: 'usdc',
        decimals: 3,
        address: '0x00e7B36aabe3FCdCe306DCD819F022A853cb5578',
      },
    ];
    this.setTokens(result);
  }
  @Action
  commitSelectedTokenInfo(payload: SelectedTokenInfo) {
    this.setSelectedTokenInfo(payload);
  }
}

export const tradeConfStore = getModule<TradeConf>(TradeConf);
