import { Contract } from '@ethersproject/contracts';
import { Web3Provider } from '@ethersproject/providers';
import ammAbi from '@/helper/abi/Amm.json';
import { BigNumber } from '@ethersproject/bignumber';

const GAS_LIMIT_BUFFER = 0.1;
/**
 * @description:
 * @param {Web3Provider} provider
 * @param {any} param2
 * @return {*}
 */
export async function AmmContract(
  provider: Web3Provider,
  [address, action, params, overrides = {}]: any
) {
  const contract: Contract = new Contract(address, ammAbi, provider.getSigner());
  try {
    // Gas estimation
    const gasLimitNumber = await contract.estimateGas[action](...params, overrides);

    const gasLimit = gasLimitNumber.toNumber();
    overrides.gasLimit = Math.floor(gasLimit * (1 + GAS_LIMIT_BUFFER));
    return await contract[action](...params, overrides);
  } catch (error) {
    return Promise.reject(error);
  }
}
