import { defHttp } from '@/utils/http/axios';

enum Api {
  TRADE_HISTORY = '/user/trade',
}

export function getTradeHistoryApi() {
  return defHttp.request({
    url: Api.TRADE_HISTORY,
    method: 'POST',
  });
}
