<template>
  <div :class="`${prefixCls}__list`">
    <div :class="`${prefixCls}__list--item`" class="text-gray-460 mt-3">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span class="text-right"></span>
    </div>
    <div class="pt-5" v-if="pendding"><UiLoading class="big" /></div>
    <UiNoRecord v-if="!lists.length && !pendding" />
    <div
      v-else
      v-for="({ name, type, volume, realized, price, fee, time }, index) in lists"
      :key="index"
      :class="`${prefixCls}__list--item`"
      class="text-white"
    >
      <span>{{ dataFormate(time) }}</span>
      <span class="uppercase">{{ name }}</span>
      <span :class="type ? 'text-green-410' : 'text-red-410'">{{
        type ? '' : ''
      }}</span>
      <span>{{ bigNumberForamte(volume) }}</span>
      <span>{{ bigNumberForamte(price, 2) }}</span>
      <span>{{ bigNumberForamte(fee) }}</span>
      <span>{{ bigNumberForamte(realized, 2) }}</span>
      <span class="text-right"><a href="" target="_blank"></a></span>
    </div>
  </div>
</template>
<script lang="ts">
  import { defineComponent, ref, computed, watch, onMounted } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { getTradeHistoryApi } from '@/api/trade/tradeHistory';
  import { dataFormate } from '@/helper/utils';
  import { bigNumberForamte } from '@/utils';
  import { accountStore } from '@/store/modules/account';
  export default defineComponent({
    name: 'userClinch',
    setup() {
      const lists = ref([]);
      const pendding = ref(false);
      const { prefixCls } = useDesign('record');
      const account = computed(() => {
        const { connector, address } = accountStore.getAccountState;
        if (!connector || !connector.id || !address) {
          return '';
        }
        return address;
      });
      async function fetchUserClinch() {
        try {
          pendding.value = true;
          const result = await getTradeHistoryApi();
          lists.value = result || [];
          pendding.value = false;
        } catch (error) {
          console.error(error);
        } finally {
          pendding.value = false;
        }
      }
      watch(account, (nVal, oVal) => {
        if (nVal) {
          if (nVal !== oVal) {
            fetchUserClinch();
          }
        } else {
          lists.value = [];
        }
      });
      onMounted(() => {
        account.value && fetchUserClinch();
      });
      return { prefixCls, lists, dataFormate, bigNumberForamte, pendding };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-record';
  .@{prefix-cls} {
    &__list {
      &--item {
        margin-bottom: 13px;
        &:last-child {
          @apply mb-0;
        }
        @apply flex flex-row text-left text-xs justify-between;
        span {
          &:first-child {
            flex-grow: 1.8;
          }
          line-height: 14px;
          @apply flex-1;
          &:last-child {
            flex-shrink: 0.5;
            @aplpy text-right;
          }
        }
      }
    }
  }
</style>
