<template>
  <div :class="`${prefixCls}`">
    <div :class="`${prefixCls}__tabs`">
      <span
        @click="handleChangeTab(positionType.BUY)"
        :class="{ green: tabIndex == positionType.BUY }"
        ></span
      >
      <span
        @click="handleChangeTab(positionType.SELL)"
        :class="{ red: tabIndex == positionType.SELL }"
        ></span
      >
    </div>
    <div class="mt-6">
      <div :class="`${prefixCls}__input`">
        <p :class="`${prefixCls}__input--title`">
          <span class="text-xs"></span>
        </p>
        <div :class="`${prefixCls}__input--wrap`">
          <input
            :value="volume"
            type="text"
            placeholder="0.0"
            class="placeholder-gray-440 text-gray-440"
            @input="handleChange($event, InputType.VOLUME)"
          />
          <div :class="`${prefixCls}__input--options`">
            <span class="text-gray-440 ml-4 text-sm uppercase">{{ tokenInfo?.coin }}</span>
          </div>
        </div>
      </div>

      <div :class="`${prefixCls}__input`" class="mt-5">
        <p :class="`${prefixCls}__input--title`">
          <span class="text-xs"></span>
        </p>
        <div :class="`${prefixCls}__input--wrap`">
          <input
            :value="amount"
            type="text"
            placeholder="0.0"
            class="placeholder-gray-440 text-gray-440"
            @input="handleChange($event, InputType.AMOUNT)"
          />
          <div :class="`${prefixCls}__input--options`">
            <a href="javascript:;" class="cursor-pointer select-none text-sm" @click="handleAll"
              ></a
            >
            <span class="text-gray-440 ml-4 text-sm">USDT</span>
          </div>
        </div>
      </div>
    </div>
    <!--  leverage ratio-->
    <div class="mt-5 pb-8">
      <SliderBar @update:ratio="updateRatio" />
    </div>

    <!--  slipPoint.vue-->
    <div class="mt-5">
      <SlipPoint @update:slippage="updateSlippage" />
    </div>

    <!--  -->
    <div class="mt-5">
      <ul :class="`${prefixCls}__detail`">
        <li>
          <div></div>
          <div>{{ openPrice }} <span>USDT</span></div>
        </li>
        <li>
          <div></div>
          <div>{{ tradeFee }} <span>USDT</span></div>
        </li>
        <li>
          <div></div>
          <div>{{ total }} <span>USDT</span></div>
        </li>
        <li>
          <div></div>
          <div>{{ userBalance }} <span>USDT</span></div>
        </li>
      </ul>
    </div>
    <!--  -->
    <div class="mt-9 pb-8">
      <UiButton
        :disabled="disabled"
        :loading="transactionPending"
        :class="[tabIndex === positionType.BUY ? 'green' : 'red', disabled ? 'disabled' : '']"
        @click="handleClick"
      >
        {{ text }}
      </UiButton>
      <p class="text-xs text-red-420 mt-5">{{ errorText }}</p>
    </div>
  </div>
</template>
<script lang="ts">
  import { defineComponent, ref, computed, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import SlipPoint from './Slippage.vue';
  import SliderBar from './SliderBar.vue';
  import { loadingBarStore, LoadingStatus } from '@/store/modules/loadingBar';
  import ClearingHouse from '@/helper/clearingHouse';
  import Web3Helper from '@/helper/web3';
  import { accountStore } from '@/store/modules/account';
  import { modalStore } from '@/store/modules/modal';
  import { Web3Provider } from '@ethersproject/providers';
  import config from '@/config';
  import BigNumber from 'bignumber.js';
  import { scale } from '@/utils';
  import { BigNumber as BN } from '@ethersproject/bignumber';
  import { notifyStore, NotifyType } from '@/store/modules/notifications';
  import { formateInput } from '@/utils';
  import { positionApi } from '@/api/trade/position';
  import { AmmContract } from '@/helper/amm';
  import { tradeConfStore, SelectedTokenInfo } from '@/store/modules/tradeConf';
  import {
    ValidationError,
    validateNumberInput,
    PositionValidation,
    InputType,
    Type,
    positionType,
  } from '@/utils/validation';
  import { formatEther, parseEther, parseUnits } from '@ethersproject/units';

  export default defineComponent({
    name: 'trading',
    components: { SlipPoint, SliderBar },
    setup() {
      const tabIndex = ref<positionType>(positionType.BUY);
      const slipPoint = ref<number>(0.5);
      const leverage = ref(5);
      // 
      const rate = ref(0.01);
      // 
      const price = ref(52907.09);
      // quoteAssetAmount   
      const amount = ref('');
      /**
       * token
       * volume    volume =  *  / 
       */
      const volume = ref('');
      const { prefixCls } = useDesign('trading');
      const tokenInfo = computed<SelectedTokenInfo | null>(() => {
        return tradeConfStore.getSelectedTokenInfo;
      });
      const handleChangeTab = (index: number) => {
        tabIndex.value = index;
      };
      // 
      const updateSlippage = (val: number) => {
        slipPoint.value = val;
      };
      // 
      const updateRatio = (val: number) => {
        leverage.value = val;
      };
      // 
      const openPrice = computed(() => {
        const num = new BigNumber(amount.value)
          .times(new BigNumber(leverage.value))
          .div(new BigNumber(volume.value));
        if (num.isNaN()) return '0';
        return num.toNumber().toFixed(2);
      });
      const unlock = async () => {
        loadingBarStore.setLoading(LoadingStatus.POSITION);
        try {
          const provider = await accountStore.getProvider();
          const result = await Web3Helper.approve(provider as Web3Provider);
          console.log('succ');
        } catch (error) {
          console.log('fail');
        } finally {
          loadingBarStore.setLoading(LoadingStatus.NONE);
        }
      };
      const activeInput = computed(() => {
        return amount.value || volume.value;
      });
      // 
      const tradeFee = computed(() => {
        const num = new BigNumber(amount.value).times(new BigNumber(rate.value));
        if (num.isNaN()) return 0;
        return num.toNumber().toFixed(2);
      });
      // 
      const total = computed(() => {
        const num = new BigNumber(amount.value)
          .times(new BigNumber(rate.value))
          .plus(new BigNumber(amount.value));
        if (num.isNaN()) return 0;
        return num.toNumber().toFixed(2);
      });
      const userBalance = computed(() => {
        const { balances, address } = accountStore.getAccountState;
        if (!address || !balances[address]) return 0;

        return scale(new BigNumber(balances[address].toString()), -6).toString();
      });
      const account = computed(() => {
        const { connector, address } = accountStore.getAccountState;
        if (!connector || !connector.id || !address) {
          return '';
        }
        return address;
      });
      const type = computed(() => {
        if (!account.value) {
          return Type.Connect;
        }
        if (!isUnlocked.value) {
          return Type.Unlock;
        } else {
          return Type.Trade;
        }
      });
      const transactionPending = computed(() => {
        return loadingBarStore.getLoadingState === LoadingStatus.POSITION;
      });
      const disabled = computed(() => {
        if (type.value !== Type.Connect) return validation.value !== PositionValidation.NONE;
        return false;
        // if (type.value === Type.Connect) {
        //   const { connector, address } = accountStore.getAccountState;
        //   return !!connector && !!connector.id && !address;
        // } else {
        //   return validation.value !== PositionValidation.NONE;
        // }
      });
      const text = computed(() => {
        return actionText.value;
        // if (loading.value) {
        //   return actionText.value;
        // }
        // if (disabled.value) {
        //   return actionText.value;
        // } else {
        //   return actionText.value;
        // }
      });
      const actionText = computed(() => {
        if (type.value === Type.Connect) {
          return 'Connect Wallet';
        }
        if (type.value === Type.Unlock) {
          return 'Unlock';
        }
        if (type.value === Type.Trade) {
          return tabIndex.value === positionType.BUY ? '' : '';
        }
        return '';
      });
      const errorText = computed(() => {
        if (validation.value === PositionValidation.WRONG_NETWORK) {
          return 'Wrong network';
        }
        // @note 
        // if (validation.value === PositionValidation.EMPTY_INPUT) {
        //   return '';
        // }
        if (validation.value === PositionValidation.INVALID_INPUT) {
          return '';
        }
        if (validation.value === PositionValidation.TOO_MIN) {
          return '10USDT';
        }
        if (validation.value === PositionValidation.TOO_MAX) {
          return '1000000USDT';
        }
        if (validation.value === PositionValidation.INSUFFICIENT_BALANCE) {
          return '';
        }
        return '';
      });
      const loading = computed(() => {
        if (type.value === Type.Connect) {
          const { connector, address } = accountStore.getAccountState;
          return !!connector && !!connector.id && !address;
        } else {
          return false;
        }
      });
      const isUnlocked = computed(() => {
        const { allowances, metaData, address } = accountStore.getAccountState;

        const { clearingHouse, assets } = config.address;
        if (
          parseFloat(volume.value.toString()) <= 0 ||
          isNaN(parseFloat(volume.value.toString()))
        ) {
          return true;
        }
        if (!allowances[clearingHouse]) {
          return true;
        }
        const allowance = allowances[clearingHouse][assets];

        if (!allowance) {
          return true;
        }
        const decimals = metaData[address].decimals;

        if (!decimals) {
          return true;
        }
        const allowanceNumber = new BigNumber(allowance.toString());
        const allowanceRaw = scale(allowanceNumber, -decimals);

        return allowanceRaw.gte(volume.value);
      });
      const validation = computed(() => {
        // Invalid input
        const { balances, chainId, metaData, address } = accountStore.getAccountState;
        const { clearingHouse, assets } = config.address;
        const inputError = validateNumberInput(activeInput.value.toString());
        // No account
        if (!account.value) {
          return PositionValidation.NO_ACCOUNT;
        }
        // Wrong network
        if (config.chainId !== chainId) {
          return PositionValidation.WRONG_NETWORK;
        }
        if (inputError === ValidationError.EMPTY) {
          return PositionValidation.EMPTY_INPUT;
        }

        if (new BigNumber(amount.value).lt(new BigNumber(10))) {
          return PositionValidation.TOO_MIN;
        }
        if (new BigNumber(amount.value).gt(new BigNumber(1000000))) {
          return PositionValidation.TOO_MAX;
        }
        if (inputError !== ValidationError.NONE) {
          return PositionValidation.INVALID_INPUT;
        }

        // Insufficient balance
        const assetMetadata = metaData[address];
        if (!assetMetadata) {
          return PositionValidation.INSUFFICIENT_BALANCE;
        }
        const assetDecimals = assetMetadata.decimals;
        const assetBalanceRaw = new BigNumber(balances[address].toString());

        const assetBalance = scale(assetBalanceRaw, -assetDecimals);
        const assetAmount = new BigNumber(activeInput.value);

        if (!assetBalance || assetAmount.gt(assetBalance)) {
          return PositionValidation.INSUFFICIENT_BALANCE;
        }
        return PositionValidation.NONE;
      });
      function handleAll() {
        amount.value = userBalance.value as string;
      }
      async function handleChange(e: any, type: InputType) {
        // 
        const value = formateInput(e.target.value);
        e.target.value = value;

        if (!value || isNaN(parseFloat(value))) {
          amount.value = '';
          volume.value = '';
          return;
        }
        if (type === InputType.AMOUNT) {
          amount.value = value;
          const provider = (await accountStore.getProvider()) as Web3Provider;
          const result = await AmmContract(provider, [
            '0x00e7B36aabe3FCdCe306DCD819F022A853cb5578',
            'getInputPrice',
            [
              BN.from(tabIndex.value === positionType.BUY ? 0 : 1),
              { d: parseEther((Number(value) * leverage.value).toString()) },
            ],
          ]);
          volume.value = new BigNumber(formatEther(result.d)).toFixed(7);
          return;
        }
        if (type === InputType.VOLUME) {
          volume.value = value;
          const provider = (await accountStore.getProvider()) as Web3Provider;
          const result = await AmmContract(provider, [
            '0x00e7B36aabe3FCdCe306DCD819F022A853cb5578',
            'getOutputPrice',
            [
              BN.from(tabIndex.value === positionType.BUY ? 0 : 1),
              { d: parseEther((Number(value) * leverage.value).toString()) },
            ],
          ]);
          amount.value = new BigNumber(formatEther(result.d)).toFixed(2);
          return;
        }
      }
      async function handleClick() {
        if (type.value === Type.Connect) {
          // @todo metamask
          connect();
        } else if (type.value === Type.Unlock) {
          // 
          unlock();
        } else {
          console.log(validation.value);
          position();
          // @todo 
        }
      }
      function connect() {
        modalStore.setConnectorModalState(true);
      }
      async function position() {
        if (
          validation.value === PositionValidation.EMPTY_INPUT ||
          validation.value === PositionValidation.INVALID_INPUT ||
          !tokenInfo.value?.address
        ) {
          return '';
        }
        try {
          loadingBarStore.setLoading(LoadingStatus.POSITION);
          const provider = (await accountStore.getProvider()) as Web3Provider;
          const tx = await ClearingHouse.openPosition(
            provider,
            tokenInfo.value?.address,
            BN.from(tabIndex.value),
            parseEther(amount.value),
            parseEther(leverage.value.toString()),
            // @todo execution reverted: Less than minimal base token
            parseEther('0')
          );

          const transactionReceipt = await provider.waitForTransaction(tx.hash, 1);
          volume.value = '';
          amount.value = '';
        } catch (error) {
          if (error.data && error.data.message) {
            notifyStore.commitNotify([error.data.message, NotifyType.ERROR]);
          } else {
            console.error(error);
          }
        } finally {
          loadingBarStore.setLoading(LoadingStatus.NONE);
        }
      }
      const address = computed(() => {
        return accountStore.getAccountState.address;
      });
      watch(address, (val) => {
        accountStore.fetchAssets(val);
      });
      watch([leverage, amount], async (val) => {
        const provider = (await accountStore.getProvider()) as Web3Provider;
        const result = await AmmContract(provider, [
          tokenInfo.value?.address,
          'getInputPrice',
          [
            BN.from(tabIndex.value === positionType.BUY ? 0 : 1),
            { d: parseEther((Number(amount.value) * Number(leverage.value)).toString()) },
          ],
        ]);
        const resultNum = formatEther(result.d);
        if (new BigNumber(resultNum).isEqualTo(0)) {
          volume.value = '';
          return;
        }

        volume.value = new BigNumber(resultNum).toFixed(7);
      });
      return {
        prefixCls,
        tabIndex,
        tokenInfo,
        handleChangeTab,
        updateSlippage,
        updateRatio,
        handleClick,
        disabled,
        loading,
        text,
        validation,
        errorText,
        volume,
        amount,
        handleChange,
        positionType,
        userBalance,
        InputType,
        price,
        tradeFee,
        total,
        transactionPending,
        openPrice,
        handleAll,
      };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-trading';
  .@{prefix-cls} {
    border-top: 1px solid rgba(#f7f7f7, 0.1);
    &__tabs {
      @apply flex flex-row justify-between mt-7 relative;
      &:after {
        @apply absolute left-0 bottom-0 block w-full bg-gray-410 opacity-10;
        height: 2px;
        content: '';
      }
      span {
        border-bottom: 3px solid transparent;
        @apply flex-1 cursor-pointer text-lg pb-1 text-gray-460 text-center;
        &.green {
          @apply border-green-410 text-green-410;
        }
        &.red {
          @apply border-red-410 text-red-410;
        }
      }
    }

    &__detail {
      @apply text-gray-460 text-xs mt-2;
      li {
        @apply flex flex-row justify-between mt-3;
      }
      span {
        @apply text-white;
      }
    }

    &__input {
      input {
        height: 36px;
        border-color: rgba(#f7f7f7, 0.1);
        @apply bg-gray-480 text-gray-460;
      }
      &--title {
        @apply text-base text-gray-460 text-left;
      }
      &--wrap {
        @apply relative mt-1;
        height: 40px;
      }
      input {
        @apply rounded-base w-full border h-full outline-none pl-4 leading-none;
      }
      &--options {
        @apply absolute flex flex-row items-end leading-5 right-4 top-1/2 text-base;
        margin-top: -10px;
      }
    }
  }
</style>
