<template>
  <div :class="`${prefixCls}`">
    <div :class="`${prefixCls}__tabs`">
      <span
        :class="{ active: tabIndex === index }"
        v-for="(item, index) in tabLists"
        :key="index"
        @click="handleChageTab(index)"
        >{{ item }}</span
      >
    </div>
    <Hold v-if="tabIndex === 0" />
    <UserClinch v-if="tabIndex === 1" />
    <Fee v-if="tabIndex === 2" />
  </div>
</template>
<script lang="ts">
  import { defineComponent, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import Hold from './hold.vue';
  import UserClinch from './UserClinch.vue';
  import Fee from './Fees.vue';
  export default defineComponent({
    name: 'userRecord',
    components: { Hold, UserClinch, Fee },
    data() {
      return {
        tabLists: ['[0]', '', ''],
      };
    },
    setup() {
      const tabIndex = ref(0);
      const { prefixCls } = useDesign('user-record');
      const handleChageTab = (index: number) => {
        tabIndex.value = index;
      };
      return { prefixCls, tabIndex, handleChageTab };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-user-record';
  .@{prefix-cls} {
    @apply flex-1 mt-1 px-8;
    &__tabs {
      height: 40px;

      @apply relative flex flex-row;
      &:after {
        content: '';
        height: 1px;
        background: rgba(#f7f7f7, 0.1);
        @apply block absolute w-full left-0 bottom-0;
      }
      span {
        line-height: 40px;
        margin-right: 30px;
        border-bottom: 3px solid transparent;
        @apply cursor-pointer select-none text-gray-460;
        &.active {
          @apply text-white;
          border-image: linear-gradient(90deg, var(--color-primary), var(--color-second)) 1 1;
        }
      }
    }
  }
</style>
