<template>
  <div :class="`${prefixCls}`">
    <!--  -->
    <UiLoading class="big" style="height: 458px;" v-if="loading" />
    <!--  k -->
    <div :class="`${prefixCls}__mask`" v-if="chartMask" @click="chartMask = false"></div>
    <!-- K -->
    <div v-show="!loading" id="tv_chart_container" style="height: 458px;"></div>
  </div>
</template>
<script lang="ts">
  // @todo definition for rule '@typescript-eslint/camelcase' was not found eslint(@typescript-eslint/camelcase)
  /* eslint-disable */
  import kLine from './kLine';

  import { ButtonHTMLAttributes, computed, defineComponent, onMounted, ref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { DataFeed, widget, GetBarsParams } from '@/lib/datafeed';
  import Storage from '@/utils/storage';
  import { bigNumberForamte } from '@/utils';
  import {
    IChartingLibraryWidget,
    Bar,
    LibrarySymbolInfo,
    StudyOverrides,
    CreateButtonOptions,
    StudyInputValue,
  } from '@/lib/library.min';
  import { ws } from '@/utils/http/ws/socket';
  import { getOverrides } from '@/lib/overrides';
  interface ButtonItem {
    title: string;
    resolution: string;
    interval: string;
    chartType: number;
    show: boolean;
  }
  // 
  const intervalMap = {
    '1min': '1',
    '5min': '5',
    // '15min': '15',
    // '30min': '30',
    '1hour': '60',
    // '4hour': '240',
    '1day': '1D',
    // '1week': '1W',
    // '1mon': '1M',
  };
  // 
  const intervalButton = {
    '1min': '1m',
    '5min': '5m',
    '15min': '15m',
    '30min': '30m',
    '1hour': '1h',
    '4hour': '4h',
    '1day': 'D',
    '1week': 'W',
    '1mon': 'M',
  };
  const buttons: ButtonItem[] = [
    {
      title: '',
      resolution: '1',
      interval: '1min',
      chartType: 3,
      show: true,
    },
    {
      title: '1m',
      resolution: '1',
      interval: '1min',
      chartType: 1,
      show: true,
    },
    {
      title: '5m',
      resolution: '5',
      interval: '5min',
      chartType: 1,
      show: false,
    },
    {
      title: '15m',
      resolution: '15',
      interval: '15min',
      chartType: 1,
      show: false,
    },
    {
      title: '30m',
      resolution: '30',
      interval: '30min',
      chartType: 1,
      show: false,
    },
    {
      title: '1h',
      resolution: '60',
      interval: '1hour',
      chartType: 1,
      show: true,
    },
    {
      title: '4h',
      resolution: '240',
      interval: '4hour',
      chartType: 1,
      show: false,
    },
    {
      title: 'D',
      resolution: '1D',
      interval: '1day',
      chartType: 1,
      show: true,
    },
    {
      title: '5D',
      resolution: '5D',
      interval: '5day',
      chartType: 1,
      show: false,
    },
    {
      title: 'W',
      resolution: '1W',
      interval: '1week',
      chartType: 1,
      show: false,
    },
    {
      title: 'M',
      resolution: '1M',
      interval: '1mon',
      chartType: 1,
      show: false,
    },
  ];
  type IntervalT = keyof typeof intervalMap;
  type IntervalButtonT = keyof typeof intervalButton;
  export default defineComponent({
    name: 'chartWidget',
    props: {
      symbolInfo: Object,
    },
    setup(props, context) {
      const { prefixCls } = useDesign('chart');
      const loading = ref(true);
      const chartMask = ref(false);
      const symbol = ref(`${props.symbolInfo?.coin}${props.symbolInfo?.market}`);
      const pricescale = computed(() => {
        return Math.pow(10, props.symbolInfo?.decimals);
      });
      const coin = computed(() => {
        return props.symbolInfo?.coin;
      });
      const market = computed(() => {
        return props.symbolInfo?.market;
      });
      const interval = ref<IntervalT>(Storage.get('tradingview.resolution') || '1hour');
      const _widget = ref<IChartingLibraryWidget>();
      const studies = ref<any[]>([]);
      // k
      const getKLine = () => {
        return new Promise((r, j) => {
          ws.subscribe(
            `${coin.value}4${market.value}${interval.value}`,
            { cmd: 4, c: coin.value, m: market.value, t: interval.value },
            (data: any) => {
              r(data);
            }
          );
        });
      };
      // k
      const subscribeKLine = () => {
        ws.subscribe(
          `${coin.value}5${market.value}${interval.value}`,
          { cmd: 5, c: coin.value, m: market.value, t: interval.value },
          (data: any) => {
            const tick = data.tick;
            // datafeed.updateKLine({
            //   time: tick.id * 1000,
            //   open: tick.open,
            //   high: tick.high,
            //   low: tick.low,
            //   close: tick.close,
            //   volume: tick.vol,
            // });
          }
        );
      };
      // 
      const unsubscribeKLine = () => {
        ws.unsubscribe('market.btcusdt.kline.5min');
      };
      // 
      const resolveSymbol = () => {
        return new Promise<any>((resolve) => {
          resolve({
            name: symbol.value.toLocaleUpperCase(),
            full_name: symbol.value.toLocaleUpperCase(),
            description: symbol.value.toLocaleUpperCase(),
            type: symbol.value,
            session: '24x7',
            exchange: 'Rainbow',
            listed_exchange: symbol.value,
            timezone: 'Asia/Shanghai',
            format: 'price',
            pricescale: pricescale.value,
            minmov: 1,
            volume_precision: 2,
            has_intraday: true,
            supported_resolutions: ['1', '5', '15', '30', '60', '240', 'D', '1W', '1M'],
          });
        });
      };
      const getBars = async (params: GetBarsParams) => {
        console.log('getBars');

        const size = window.innerWidth;
        if (params.resolution !== intervalMap[interval.value]) {
          unsubscribeKLine();
          for (const key in intervalMap) {
            if (intervalMap[key as IntervalT] === params.resolution) {
              interval.value = key as IntervalT;
            }
          }
        }

        // 'history_kline', void 0, {
        //   params: {
        //     symbol: symbol,
        //     period: interval.value,
        //     size: size > 2000 ? 2000 : size,
        //   },
        // }

        const res: any = await getKLine();

        if (
          params.resolution === intervalMap[interval.value] &&
          params.firstDataRequest &&
          res.length
        ) {
          subscribeKLine();
        }

        if (!res.length) {
          return {
            bars: [],
            meta: { noData: true },
          };
        }
        const list: any = [];
        for (let i = 0; i < res.length; i++) {
          const item = res[i];
          list.push({
            time: item.time * 1000,
            open: bigNumberForamte(item.open),
            high: bigNumberForamte(item.high),
            low: bigNumberForamte(item.low),
            close: bigNumberForamte(item.close),
            volume: bigNumberForamte(item.vol),
          });
        }

        list.sort((l, r) => (l.time > r.time ? 1 : -1));
        return {
          bars: list,
          meta: {
            noData: !list.length,
          },
        };
      };
      const datafeed = new DataFeed({
        getBars: (params) => getBars(params),
        fetchResolveSymbol: () => resolveSymbol(),
      });
      // 
      const createStudy = async () => {
        const MA7 = await _widget.value
          ?.chart()
          .createStudy('Moving Average', false, false, [7], { 'Plot.color': '#0ab2d2' });
        const MA30 = await _widget.value
          ?.chart()
          .createStudy('Moving Average', false, false, [30], { 'Plot.color': '#dc4273' });
        const MA60 = await _widget.value
          ?.chart()
          .createStudy('Moving Average', false, false, [60], { 'plot.color': '#ff8200' });

        studies.value.push(MA7);
        studies.value.push(MA30);
        studies.value.push(MA60);
      };
      // 
      const createButton = () => {
        const chartTypeCache = (Storage.get('tradingview.chartType') || '1') * 1;
        for (let i = 0; i < buttons.length; i++) {
          if (!buttons[i].show) continue;
          ((button) => {
            const { title, resolution, chartType } = button;
            const buttonElement = _widget.value?.createButton() as HTMLElement;
            buttonElement.className += `${
              resolution === intervalMap[interval.value] && button.chartType === chartTypeCache
                ? ' active'
                : ''
            }`;
            buttonElement.setAttribute('title', title);
            buttonElement.textContent = title;
            buttonElement.addEventListener('click', function () {
              Storage.set('tradingview.resolution', button.interval);
              Storage.set('tradingview.chartType', chartType);
              const activeEle = this.parentNode?.parentNode?.parentNode?.querySelector(
                '.active'
              ) as HTMLElement;
              if (activeEle) {
                activeEle.className = activeEle.className.replace(/(\sactive|active\s)/, '');
              }
              buttonElement.className += ' active';

              if (chartType !== _widget.value?.chart().chartType()) {
                _widget.value?.activeChart().setChartType(chartType);
                // toggleStudy(chartType);
              } else {
                _widget.value?.chart().setResolution(resolution, function onReadyCallback() {
                  interval.value = resolution as IntervalT;
                });
              }
            });
          })(buttons[i]);
        }
      };
      //
      const toggleStudy = (chartType: number) => {
        studies.value.forEach((study) => {
          _widget.value
            ?.chart()
            .getStudyById(study)
            .setVisible(chartType === 3 ? false : true);
        });
      };
      // k
      const initTradingView = () => {
        const chartType = (Storage.get('tradingview.chartType') || '1') * 1;
        _widget.value = new widget({
          debug: false,
          fullscreen: false,
          symbol: symbol.value,
          interval: intervalMap[interval.value],
          /* eslint-disable @typescript-eslint/camelcase */
          container_id: 'tv_chart_container',
          datafeed: datafeed,
          /* eslint-disable @typescript-eslint/camelcase */
          library_path: '/charting_library/',
          locale: 'zh',
          theme: 'Dark',
          timezone: 'Asia/Shanghai',
          autosize: true,
          height: 458,
          /* eslint-disable @typescript-eslint/camelcase */
          enabled_features: ['hide_last_na_study_output', 'side_toolbar_in_fullscreen_mode'],
          /* eslint-disable @typescript-eslint/camelcase */
          custom_css_url: '/charting_library/static/chart.css',
          /* eslint-disable @typescript-eslint/camelcase */
          disabled_features: [
            'header_symbol_search',
            'header_saveload',
            'header_screenshot',
            'header_chart_type',
            'header_compare',
            'header_undo_redo',
            'timeframes_toolbar',
            'volume_force_overlay',
            'header_resolutions',
            '14851',
            // 'header_indicators', //
            // 'header_settings', //
          ],
        });

        _widget.value?.onChartReady(async () => {
          await _widget.value?.headerReady();

          createStudy();
          createButton();
          toggleStudy(chartType);
          _widget.value?.chart().setChartType(chartType);
          _widget.value?.applyOverrides(getOverrides('black'));
          loading.value = false;
          chartMask.value = true;
        });
      };
      // 
      const setSymbol = (_symbol) => {
        unsubscribeKLine();
        symbol.value = _symbol;
        _widget.value?.setSymbol(_symbol, intervalMap[interval.value], () => {
          console.log('------setSymbol---------', _symbol);
        });
      };
      onMounted(() => {
        initTradingView();
      });
      return { prefixCls, loading, chartMask, setSymbol, symbol };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-chart';
  .@{prefix-cls} {
    height: 458px;
    @apply mt-1 bg-gray-490 relative;
    &__mask {
      @apply absolute left-0 right-0 top-0 bottom-0 z-10;
    }
  }
</style>
