
  import { defineComponent, watch, ref, computed, onMounted } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { getFundingPaymentApi } from '@/api/trade/fundingPayment';
  import { dataFormate } from '@/helper/utils';
  import { scale, bigNumberForamte } from '@/utils';
  import { accountStore } from '@/store/modules/account';
  import BigNumber from 'bignumber.js';
  export default defineComponent({
    name: 'fee',
    setup() {
      const { prefixCls } = useDesign('record');
      const lists = ref([]);
      const pendding = ref(false);

      const account = computed(() => {
        const { connector, address } = accountStore.getAccountState;
        if (!connector || !connector.id || !address) {
          return '';
        }
        return address;
      });
      async function fetchFundingPayment() {
        try {
          pendding.value = true;
          const result = await getFundingPaymentApi();
          lists.value = result;
          pendding.value = false;
        } catch (error) {
          console.error(error);
        } finally {
          pendding.value = false;
        }
      }
      watch(account, (nVal, oVal) => {
        if (nVal) {
          if (nVal !== oVal) {
            fetchFundingPayment();
          }
        } else {
          lists.value = [];
        }
      });
      onMounted(() => {
        account.value && fetchFundingPayment();
      });
      return { prefixCls, lists, dataFormate, bigNumberForamte, pendding };
    },
  });
