
  import { defineComponent, ref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { tradeConfStore } from '@/store/modules/tradeConf';
  import { ws } from '@/utils/http/ws/socket';
  import { dataFormate } from '@/helper/utils';
  import { bigNumberForamte } from '@/utils';
  export default defineComponent({
    name: 'tradingRecord',
    setup() {
      const { prefixCls } = useDesign('trading-record');
      const list = ref([]);
      watch(
        () => tradeConfStore.getSelectedTokenInfo,
        (val) => {
          if (val) {
            const { coin, market } = val;
            ws.subscribe(`${coin}2${market}`, { cmd: 2, c: coin, m: market }, (data) => {
              console.log('---:', data);
              list.value = data;
            });
          }
        }
      );
      return { prefixCls, list, dataFormate, bigNumberForamte };
    },
  });
