
  import { defineComponent, ref, unref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  export default defineComponent({
    name: 'slippage',
    emits: ['update:slippage'],
    data() {
      return {
        slippageLists: [0.01, 0.5, 1],
      };
    },
    setup(_, { emit }) {
      const slippage = ref<number>(0.5);
      const slipPointIpt = ref();
      const { prefixCls } = useDesign('slip-point');
      const handleClick = (val: number) => {
        slippage.value = val;
      };
      watch(slippage, (nVal, oVal) => {
        emit('update:slippage', nVal);
      });
      return { prefixCls, slippage, handleClick };
    },
  });
