<template>
  <div>
    <Header />
    <div :class="`${prefixCls}`">
      <div :class="`${prefixCls}__container--left`">
        <Info />
      </div>
      <div :class="`${prefixCls}__container--right`">
        <Trade />
      </div>
    </div>
    <Teleport to="#modals">
      <ModalConnectors :open="isOpen" @close="handleClose" />
    </Teleport>
  </div>
</template>
<script lang="ts">
  import { computed, defineComponent, onMounted } from 'vue';
  import Trade from './Trade.vue';
  import Info from './Info.vue';
  import { useDesign } from '@/hooks/useDesign';
  import { tradeConfStore } from '@/store/modules/tradeConf';
  import { modalStore } from '@/store/modules/modal';
  export default defineComponent({
    name: 'container',
    components: {
      Trade,
      Info,
    },
    setup() {
      const { prefixCls } = useDesign('trade-wrap');
      const isOpen = computed(() => {
        return modalStore.getConnectorsModalState;
      });

      function handleClose() {
        modalStore.setConnectorModalState(false);
      }
      onMounted(() => {
        // 
        tradeConfStore.fetchTokens();
      });
      return { prefixCls, isOpen, handleClose };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-trade-wrap';
  .@{prefix-cls} {
    @apply flex flex-row;
    &__container {
      &--left {
        flex: 1;
        margin-right: 4px;
      }
      &--right {
        width: 300px;
        min-width: 260px;
        @apply bg-gray-490;
      }
    }
  }
</style>
